.configured{
      width: 100%;
      height: 100%;
      flex-direction: column;
      background: #eee;
      position: fixed;
      z-index: 20;
      top: 0;
      left: 0;
}

.configured h1{
      font-size: 4rem;
      margin-top: 1rem;
      color: #d4d4d4;
}

.icon_folder{
      width: fit-content;
      height: fit-content;
      margin-top: 2rem
}

.icon_folder *{
      font-size: 8rem;
      color: #a3a3a3;
}

.configured > p{
      margin-top: 1rem;
      color: #222;
      font-size: 2.4rem;
      text-align: center;
}

.configured > p strong{
      font-size: 2.4rem;
      color: #3b00bc;
}

.configured > a{
      margin-top: 1rem;
      background: #1a1a1a;
      padding: .2rem;
      height: 5.8rem;
      width: 20rem;
      border-radius: .6rem;
}

.configured > a span{
      width: 100%;
      height: 4.5rem;
      display: flex;
      background: #3b00bc;
      border-radius: .4rem;
      text-transform: uppercase;
      align-items: center;
      justify-content: center;
      color: #eee;
      font-size: 2rem;
}