.inner_video{
      width: 100%;
      height: 45rem;
      margin-top: 2rem;
}

.videos_list{
      width: 100%;
      height: auto;
}

.video_row{
      width: 100%;
      height: 29.4rem;
      position: relative;
      margin-bottom: 3rem;
}

.video_row__frame{
      width: 100%;
      height: 100%;
      border-radius: 2rem;
      overflow: hidden;
}

.video_row__frame iframe{
      border: none;
      outline: none;
}

.video_row__title{
      position: absolute;
      width: 49rem;
      height: auto;
      background: var(--theme-1);
      padding: 1.5rem;
      border-radius: .6rem;
      left: 2.5rem;
      bottom: 2rem;
      box-shadow: 2rem 2rem 20px rgba(0,0,0,.3);
      transition: .3s ease-in-out;
      opacity: 1;
}

.video_row__title:before{
      width: 1.6rem;
      height: 1.6rem;
      background: inherit;
      top: calc(50% - .8rem);
      left: -.8rem;
      transform: rotate(45deg);
}

.video_row:hover .video_row__title{
      transform: translateY(200%);
      opacity: 0;
}


.video_row__title p{
      color: var(--text-100);
      font-size: 2rem;
      text-transform: uppercase;
}