.home_gallery__grid{
      margin-top: 1rem;
      gap: 2.6rem;
}

.home_gallery__grid_img{
      height: 10rem;
      border-radius: 2rem;
      overflow: hidden;
}

.btn_gallery__img{
      width: 11rem;
      height: 4.6rem;
      border-radius: 1rem;
      position: relative;
      overflow: hidden;
      margin-left: calc(100% - 11rem);
      margin-top: 2rem;
}

.btn_gallery__more{
      width: 100%;
      height: 100%;
      background: var(--theme-1);
      border-radius: 1rem;
      overflow: hidden;
}

.btn_gallery__more img{
      filter: grayscale(1);
      mix-blend-mode: color-burn;
}

.btn_gallery__img span{
      position: absolute;
      width: 100%;
      height: 100%;
      color: var(--text-100);
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      font-weight: 600;
      letter-spacing: .1rem;
      font-size: 2rem;
}