.aside_content{
      width: 14rem;
      height: 100%;
      position: relative;
      background: var(--bg-200);
      flex-shrink: 0;
}

.aside_content__bg{
      position: absolute;
      width: 100%;
      height: 100%;
      overflow: hidden;
      top: 0;
      left: 0;
}

.aside_content__bg img{
      filter: grayscale(1) contrast(1);
      mix-blend-mode: screen;
      opacity: .4;
}

.aside_menu,
.aside_menu ul{
      width: 100%;
      height: auto;
}

.aside_menu ul li{
      margin-bottom: .2rem;
}

.menu_target{
      width: 100%;
      height: 14rem;
      flex-direction: column;
      position: relative;
      gap: .5rem;
      transition: .3s ease-in-out;
      background: transparent;
}

.menu_target:before{
      width: 0;
      height: 100%;
      top: 0;
      right: 0;
      background: var(--theme-1);
      transition: .3s ease-in-out;
}

.menu_target:hover,
.menu_target.active{
      background: rgba(255,255,255,.3);
}

.menu_target:hover:before,
.menu_target.active:before{
      width: 1rem;
}

.menu_target *{
      color: var(--text-100);
      font-size: 3.5rem;
}

.menu_target > span{
      text-transform: uppercase;
      font-size: 2rem;
}

.friends_amount{
      width: 5rem;
      height: 2.2rem;
      position: absolute;
      border-radius: 999px;
      background: var(--theme-1);
      top: 1rem;
      right: 2rem;
      align-items: center;
      gap: .4rem;
}

.friends_amount span{
      width: 2.2rem;
      height: 2.2rem;
      border-radius: 50%;
      background: rgba(0,0,0,.3);
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1rem;
      font-weight: 800;
}

.friends_amount small{
      font-size: 1rem;
      text-transform: uppercase;
      letter-spacing: .1rem;
}