.header_home{
      width: 100%;
      height: 5.6rem;
      align-items: center;
      justify-content: space-between;
}

.user_navbar{
      text-transform: uppercase;
      gap: 2rem;
      align-items: center;
}

.user_navbar a{
      color: var(--text-300);
      position: relative;
      letter-spacing: .05rem;
}

.user_navbar a:before{
      width: 0;
      height: .3rem;
      background: var(--theme-1);
      left: 50%;
      bottom: 0;
      transition: .2s ease-in-out;
}

.user_navbar a:hover:before{
      width: 100%;
      left: 0;
}