.online_content{
      gap: 1rem;
      align-items: flex-start;
}

.online_content__avatar{
      width: 5.6rem;
      height: 5.6rem;
      border-radius: 2rem;
      position: relative;
      background: var(--bg-200);
}

.online_avatar__img{
      width: 5.6rem;
      height: 5.6rem;
      border-radius: 2rem;
      overflow: hidden;
}

.online_dot{
      width: 2.6rem;
      height: 2.6rem;
      position: absolute;
      background: var(--bg-100);
      bottom: 0;
      right: -1rem;
      border-radius: 50%;
}

.online_dot.red:before,
.online_dot.green:before{
      width: 1.6rem;
      height: 1.6rem;
      border-radius: 50%;
      top: .5rem;
      left: .5rem;
}

.online_dot.green:before{
      background: var(--green);
}

.online_dot.red:before{
      background: var(--red);
}

.online_content span{
      text-transform: uppercase;
      font-weight: 500;
      font-size: 2rem;
}