.pages_content{
      position: absolute;
      width: 100%;
      height: 57rem;
      left: 0;
      bottom: 0;
      background: var(--bg-100);
      border-radius: 5rem 5rem 0 0;
      box-shadow: 0 -2rem 20px rgba(0,0,0,.2);
      z-index: 2;
      overflow: hidden;
      color: var(--text-300);
}

.inner_page{
      width: 100%;
      height: 100%;
      background: var(--bg-100);
      border-radius: 5rem 5rem 0 0;
      overflow: hidden;
      padding: 2rem 3rem;
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(10rem);
      opacity: 0;
      visibility: hidden;
      transition: .5s ease-in-out;
}

.inner_page.active{
      transform: translateY(0);
      opacity: 1;
      visibility: visible;
}

.inner_page__content{
      width: 100%;
      height: 45rem;
      margin-top: 1.5rem;
}

.pager{
      gap: 3.5rem;
      justify-content: space-between;
      height: 45rem;
}

.home_title{
      width: 100%;
      height: 2rem;
      position: relative;
}

.home_title:before{
      width: 15.4rem;
      height: .8rem;
      background: var(--theme-1);
      right: 0;
      top: calc(50% - .4rem);
}

.home_title h2{
      text-transform: uppercase;
      font-weight: 500;
      letter-spacing: .1rem;
}