.header_page{
      width: 100%;
      height: 5.6rem;
      align-items: center;
      justify-content: space-between;
}

.header_page__user{
      gap: 1rem;
      align-items: center;
}

.header_page__img{
      width: 5.6rem;
      height: 5.6rem;
      border-radius: 2rem;
      overflow: hidden;
}

.header_page__info h3{
      text-transform: uppercase;
      font-size: 2rem;
      line-height: 1.6rem;
}

.header_page__info strong{
      font-size: 2rem;
      color: var(--text-200);
}

.header_pager__title{
      width: 25rem;
      position: relative;
      align-items: center;
}

.header_pager__title:before{
      width: 15.4rem;
      height: .8rem;
      background: var(--theme-1);
      right: 0;
      top: calc(50% - .4rem);
}

.header_pager__title h2{
      text-transform: uppercase;
      font-weight: 400;
      font-size: 1.8rem;
      letter-spacing: .05rem;
}