.friends_content{
      position: absolute;
      z-index: 10;
      width: 30rem;
      height: 100%;
      background: #eee;
      top: 0;
      right: 0;
      border-radius: 5rem 5rem 0 5rem;
      box-shadow: -1.5rem 0 20px rgba(0,0,0,.2);
      transform: translateX(120%);
      padding: 2rem;
      transition: .5s ease-in-out;
}

.friends_content.active{
      transform: translateX(0);
}

.friends_list__content{
      width: 100%;
      height: 48rem;
      margin-top: 1rem;
}

.friends_list{
      width: 100%;
      height: auto;
}

.friend_row{
      width: 25rem;
      height: 10rem;
      position: relative;
      margin-bottom: 1rem;
      gap: 1rem;
      align-items: center;
}

.friend_row:before{
      width: 21rem;
      height: 100%;
      background: linear-gradient(90deg, #ffffff 20%, var(--theme-1));
      border-radius: 1rem;
      top: 0;
      right: 0;
      box-sizing: border-box;
}

.friend_row:after{
      width: 20.6rem;
      height: calc(100% - .4rem);
      background: rgba(233, 233, 233, 1);
      border-radius: .8rem;
      top: .2rem;
      right: .2rem;
      box-sizing: border-box;
}

.friend_row__avatar{
      position: relative;
      width: 8rem;
      height: 8rem;
      background: var(--bg-200);
      border-radius: 2rem;
      box-shadow: 1rem 1rem 20px rgba(0,0,0,.2);
      overflow: hidden;
      z-index: 2;
}

.friend_row__info{
      position: relative;
      flex-direction: column;
      z-index: 2;
}

.friend_row__info span{
      text-transform: uppercase;
      color: var(--text-300);
      letter-spacing: .05rem;
      font-size: 1.8rem;
}

.friend_row__info a{
      width: 6.4rem;
      height: 2.8rem;
      background: var(--bg-300);
      border-radius: 999px;
      color: var(--text-100);
      text-transform: uppercase;
      border: solid .3rem var(--text-100);
      font-size: 1.2rem;
      margin-top: 1rem;
}

.friend_btn{
      margin-top: 1rem;
      width: 100%;
      justify-content: flex-end;
      gap: 1rem;
      align-items: center;
}

.friend_btn button{
      width: 20rem;
      height: 3.6rem;
      border-radius: 999px;
      background: var(--bg-300);
      color: var(--text-100);
      text-transform: uppercase;
      letter-spacing: .1rem;
}

.icon_arrow{
      width: 3.6rem;
      height: 3.6rem;
      color: var(--theme-1);
}

.icon_arrow *{
      font-size: 3rem;
}