.social_navbar{
      width: 100%;
      justify-content: center;
      gap: 1rem;
      align-items: center;
}

.btn_social{
      width: 3.4rem;
      height: 3.4rem;
      color: var(--text-200);
      transition: .2s;
}

.btn_social:hover{
      color: var(--theme-1);
}

.btn_social *{
      font-size: 2rem;
}