.photos_grid{
      gap: 2rem;
}

.photo_card{
      height: 25rem;
      position: relative;
      border-radius: 2rem;
      overflow: hidden;
}

.photo_card__img{
      width: 100%;
      height: 100%;
      background: var(--theme-1);
      overflow: hidden;
}

.photo_card__img img{
      transition: .3s;
}

.photo_card:hover .photo_card__img img{
      filter: grayscale(1);
      mix-blend-mode: color-burn;
}

.photo_card button{
      width: 5rem;
      height: 5rem;
      position: absolute;
      top: calc(50% - 2.5rem);
      left: calc(50% - 2.5rem);
      background: transparent;
      color: var(--text-100);
      transition: .2s ease-in-out;
      transform: scale(0);
}

.photo_card button *{
      font-size: 5rem;
}

.photo_card:hover button{
      transform: scale(1);
}