.relation_content__img{
      margin-top: 1rem;
      width: 100%;
      height: 10rem;
      position: relative;
      background: var(--theme-1);
      border-radius: 2rem;
      overflow: hidden;
}

.relation_content__img img{
      filter: grayscale(1);
      mix-blend-mode: color-burn;
}

.relation_content__img button{
      position: absolute;
      width: 100%;
      height: 3.4rem;
      left: 0;
      bottom: 0;
      background: rgba(0,0,0,.3);
      color: var(--text-100);
      text-transform: uppercase;
}