.banner_content{
      width: 100%;
      height: 24rem;
      background: var(--bg-200);
      position: relative;
}

.banner_content__img{
      width: 100%;
      height: 100%;
      background: var(--theme-1);
      overflow: hidden;
}

.banner_content__img img{
      filter: grayscale(1);
      mix-blend-mode: color-burn;
}

.banner_content__title{
      width: 100%;
      height: fit-content;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 2;
}

.banner_content__title h1{
      font-family: var(--ft-3);
      text-transform: uppercase;
      font-size: 12rem;
      font-weight: 800;
      line-height: 14.5rem;
}