.inner_home{
      gap: 3.5rem;
      justify-content: space-between;
}

.home_user__data{
      width: 26rem;
      height: auto;
      flex-shrink: 0;
}

.user_data__title{
      width: 100%;
}

.user_data__title h1{
      width: 100%;
      text-align: center;
      text-transform: uppercase;
      font-size: 3.4rem;
      line-height: 3rem;
}

.user_data__title span{
      display: flex;
      width: 100%;
      height: 3.2rem;
      background: var(--theme-1);
      align-items: center;
      justify-content: center;
      color: var(--text-100);
      font-size: 2.4rem;
      font-weight: 500;
}

.user_data__description{
      height: auto;
      max-height: 16rem;
      margin-top: 1rem;
}

.user_data__description p{
      white-space: pre-line;
      line-height: 2.2rem;
      font-size: 1.8rem;
}

.home_social{
      margin-top: 2rem;
}

.home_relation{
      margin-top: 1rem;
}

.home_user__images{
      width: 22.6rem;
}

.user_avatar{
      width: 22.6rem;
      height: 22.6rem;
      border-radius: 2rem;
      overflow: hidden;
}

.home_gallery{
      margin-top: 2rem;
}