.relation_col__data{
      width: 26rem;
      flex-shrink: 0;
}

.relation_data__title{
      width: 100%;
      flex-direction: column;
      align-items: center;
}

.relation_data__title h2{
      font-size: 2.4rem;
      text-transform: uppercase;
}

.relation_data__title span{
      display: flex;
      align-items: center;
      justify-content: center;
      height: 3.2rem;
      width: 100%;
      background: var(--theme-1);
      color: var(--text-100);
      font-size: 2.4rem;
      font-weight: 500;
}

.relation_description{
      margin-top: 1rem;
}

.relation_description p{
      white-space: pre-line;
      font-size: 1.8rem;
      line-height: 2rem;
}

.relation_navbar{
      margin-top: 2rem;
      gap: 2rem;
}

.relation_navbar a{
      width: 8rem;
      height: 2.6rem;
      background: var(--theme-1);
      color: var(--text-100);
      text-transform: uppercase;
      position: relative;
}

.relation_navbar a:before{
      width: 100%;
      height: .3rem;
      background: var(--text-300);
      left: 0;
      bottom: 0;
      transition: .2s;
}

.relation_navbar a:hover:before{
      transform: translateY(200%);
}

.relation_col__image{
      width: 22.6rem;
}

.relation_image{
      width: 22.6rem;
      height: 22.6rem;
      border-radius: 2rem;
      overflow: hidden;
}

.relation_type{
      width: 100%;
      align-items: center;
      position: relative;
      margin-top: 1rem;
}

.relation_type:before{
      width: 15.4rem;
      height: .8rem;
      background: var(--theme-1);
      right: 0;
      top: calc(50% - .4rem);
}

.relation_type span{
      text-transform: uppercase;
      font-size: 1.8rem;
      letter-spacing: .05rem;
}